
/* gap between items during scrolls */
.scrolly-gap {
    padding-top: 100px;
}

/* important quote */
.quote-container {
    margin-top: 40%;
    margin-left: 5%;
    margin-right: 5%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: black;
    border-radius: 2%;
    margin-bottom: 40%;
}

.quote {
    font-size: 2em;
    text-align: center;
}

.quotename {
    margin-top: -5px;
    font-size: 1.2em;
}

/* wide for images and other things */
.wide {
    margin-left: 10%;
    margin-right: 10%;
    text-align: center;
}

.wide-medium{
    margin-left: 15%;
    margin-right: 15%;
    text-align: center;
}

.wide-chart {
    width: 100%;
    text-align: center;
}

/* pop out box */
.box {
    border: 1px solid white;
    padding-top: 0.2em;
    padding-right: 1em;
    padding-left: 1em;
    padding-bottom: 0.2em;
    border-radius: 5%;
}

.box-title {
    text-decoration: underline;
}

.heading-1 {
    margin-top: 2em;
    line-height: 1.3em;
    font-weight: 300;
    color: white;
    text-align: center;
}

.heading-2 {
    text-align: center;
    font-size: 1.8em;
}

.heading-3 {
    text-align: center;
    font-size: 1.5em;
}

/* bio */
.wrap {
    float: left; 
    margin: 5px;
}

.face-img {
    width: 25%;
    height: 25%;
    margin-right: 15px;
    border-radius: 5%;
}

/* two graphs */
.two-graphs {
    margin-left: 3%;
    margin-right: 3%;
    display: flex;
}

embed {
    width:100vh;
    height: 70vh;
}
